.ng2-c-roles {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }
  @media (min-width: 768px) {
    .ng2-c-roles {
      margin-left: -10px;
      width: calc(100% + 20px); } }

.ng2-c-roles__item {
  align-items: center;
  background: #ffffff;
  border-radius: 4px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: 210px;
  justify-content: center;
  margin-bottom: 20px;
  text-align: center;
  width: 100%; }
  @media (min-width: 768px) {
    .ng2-c-roles__item {
      background: #f5f5f5;
      margin: 0 10px 20px;
      width: 280px; } }

.ng2-c-roles__item--loading {
  justify-content: flex-start;
  padding-top: 35px; }
  .ng2-c-roles__item--loading .ng2-c-roles__icon {
    display: none; }
  .ng2-c-roles__item--loading .ng2-c-roles__figure {
    background-color: #f5f5f5;
    border-radius: 50%;
    height: 65px;
    width: 65px; }
    @media (min-width: 768px) {
      .ng2-c-roles__item--loading .ng2-c-roles__figure {
        background-color: #ffffff; } }

.ng2-c-roles__figure {
  align-items: center;
  display: flex;
  margin-bottom: 20px;
  text-align: center; }

.ng2-c-roles__icon {
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  height: 65px;
  width: 65px; }

.ng2-c-roles__icon--female {
  background-image: url("./img/user-female.svg"); }

.ng2-c-roles__icon--male {
  background-image: url("./img/user-male.svg"); }

.ng2-c-roles__icon--company {
  background-image: url("./img/user-company.svg"); }

.ng2-c-roles__label {
  color: #28648a;
  display: block;
  font-size: 18px; }

.ng2-c-roles__label--title {
  font-size: 22px;
  white-space: normal;
  word-break: break-word;
  word-wrap: break-word; }
